img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "PT Sans Narrow";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("/../fonts/pt_sans_narrow_regular.woff2") format("woff2"), url("/../fonts/pt_sans_narrow_regular.woff") format("woff");
}
@font-face {
  font-family: "PT Sans Narrow";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("/../fonts/pt_sans_narrow_bold.woff2") format("woff2"), url("/../fonts/pt_sans_narrow_bold.woff") format("woff");
}
.page {
  height: 100%;
  font-size: 18px;
  font-family: "PT Sans Narrow", "Arial", sans-serif;
  line-height: 1.5;
  color: #f8f5f2;
}

.page__body {
  min-height: 100%;
  margin: 0;
  background-color: #f8f5f2;
  color: #000000;
}

.page-header {
  display: flex;
  flex-direction: column;
  background-color: #242424;
  color: #ffffff;
}
@media (min-width: 768px) {
  .page-header {
    background-color: #000000;
  }
}

.page-header__logo {
  height: 85px;
  text-align: center;
}
@media (min-width: 768px) {
  .page-header__logo {
    order: 2;
    height: 400px;
    background-color: #000000;
    background-image: url("../img/background/bg-header-tablet.jpg");
    background-repeat: no-repeat;
    background-position: 50% -9px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .page-header__logo {
    background-image: url("../img/background/bg-header-tablet@2x.jpg");
    background-size: 1338px auto;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    height: 648px;
    background-position: 50% 0;
    background-size: cover;
  }
}

.main-nav {
  position: relative;
  width: auto;
  min-height: 51px;
  background-color: #826550;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #000000;
  }
}

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: #6a4d38;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover, .main-nav__toggle:active {
  background-color: #624530;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
}

@media (max-width: 767px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .site-list__item--active {
    display: block;
  }
  .main-nav--closed .site-list__item--active a {
    padding-right: 70px;
    padding-left: 70px;
    text-align: center;
  }
  .main-nav--closed .user-list__item {
    display: none;
  }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 70px;
    z-index: 10;
    min-width: 80vh;
    background-color: #6a4d38;
  }
  .main-nav--opened .main-nav__wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 7px;
    height: 100%;
    box-shadow: inset 4px 0 7px -2px rgba(0, 0, 0, 0.4);
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
  .main-nav--nojs .site-list__item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: left;
  }
  .main-nav--nojs .user-list__item {
    display: block;
    background-color: #6a4d38;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: auto;
  left: 0;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (min-width: 768px) {
  .main-nav__wrapper {
    display: flex;
    justify-content: space-between;
    /* 640px / 768px ~= 84% */
    width: 84%;
    /* 960px / .84 ~= 1142px (центровка начнётся чуть раньше 1200px) */
    max-width: 960px;
    margin: 0 auto;
  }
}

.site-list {
  font-weight: bold;
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 570px;
    flex-grow: 1;
  }
}
@media (min-width: 1200px) {
  .site-list {
    width: 780px;
  }
}

@media (min-width: 768px) {
  .site-list__item {
    margin-right: 32px;
  }
  .site-list__item:last-child {
    margin-right: 0;
  }
  .site-list__item--active {
    position: relative;
  }
  .site-list__item--active::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 3px;
    background: #826550;
  }
}
@media (min-width: 1200px) {
  .site-list__item {
    margin-right: 0;
  }
  .site-list__item--active::after {
    right: 30px;
    left: 30px;
  }
}

@media (min-width: 768px) {
  .site-list__item a[href]:hover {
    color: #826550;
    background-color: #000000;
  }
  .site-list__item a[href]:active {
    color: #ffffff;
    background-color: #000000;
    opacity: 0.3;
  }
}
@media (min-width: 1200px) {
  .site-list__item a[href]:hover {
    color: #000000;
    background-color: #ffffff;
  }
  .site-list__item a[href]:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
    opacity: 1;
  }
}

.site-list a {
  position: relative;
  display: block;
  padding: 12px 21px;
  padding-bottom: 13px;
  font-size: 18px;
  line-height: 1.33;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #826550;
  border-top: 1px solid #886e5b;
  border-bottom: 1px solid #71543f;
}
@media (min-width: 768px) {
  .site-list a {
    padding: 0;
    padding-top: 25px;
    padding-bottom: 23px;
    background: none;
    border: none;
  }
}
@media (min-width: 1200px) {
  .site-list a {
    padding-right: 31px;
    padding-left: 31px;
  }
}

.site-list__icon-comb {
  position: absolute;
  top: 15px;
  left: auto;
  right: 15px;
  width: 16px;
  height: 16px;
  opacity: 0.3;
  fill: #ffffff;
}
@media (min-width: 768px) {
  .site-list__icon-comb {
    top: 8px;
    left: 0;
    right: 0;
    margin: auto;
  }
  .site-list a[href] .site-list__icon-comb {
    opacity: 1;
    fill: #826550;
  }
  .site-list a[href]:active .site-list__icon-comb {
    opacity: 0.9;
    fill: #ffffff;
  }
}
@media (min-width: 1200px) {
  .site-list__icon-comb {
    top: 28px;
    left: 8px;
    margin: 0;
  }
  .site-list a[href]:hover .site-list__icon-comb {
    opacity: 0.3;
    fill: #000000;
  }
  .site-list a[href]:active .site-list__icon-comb {
    opacity: 0.1;
  }
}

@media (min-width: 768px) {
  .user-list {
    width: 60px;
  }
}
@media (min-width: 1200px) {
  .user-list {
    width: auto;
    max-width: 160px;
  }
}

.user-list__login {
  position: relative;
  display: block;
  padding: 17px 21px;
  padding-left: 49px;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none;
}
@media (min-width: 768px) {
  .user-list__login {
    width: 60px;
    height: 70px;
    padding: 0;
  }
  .user-list__login:hover .user-list__login-icon {
    opacity: 1;
    fill: #826550;
  }
  .user-list__login:active .user-list__login-icon {
    opacity: 0.3;
    fill: #ffffff;
  }
  .user-list__login span {
    position: absolute;
    opacity: 0;
  }
}
@media (min-width: 1200px) {
  .user-list__login {
    position: relative;
    width: auto;
    height: auto;
    padding-top: 25px;
    padding-right: 30px;
    padding-bottom: 23px;
    padding-left: 50px;
    font-size: 18px;
    line-height: 1.33;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
  }
  .user-list__login:hover {
    color: #000000;
    background-color: #ffffff;
  }
  .user-list__login:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
  }
  .user-list__login:hover .user-list__login-icon {
    opacity: 0.3;
    fill: #000000;
  }
  .user-list__login:active .user-list__login-icon {
    opacity: 0.1;
  }
  .user-list__login span {
    position: static;
    opacity: 1;
  }
}

.user-list__login-icon {
  position: absolute;
  top: 17px;
  left: 21px;
  width: 16px;
  height: 16px;
  opacity: 0.3;
  fill: #ffffff;
}
@media (min-width: 768px) {
  .user-list__login-icon {
    top: 29px;
    left: 12px;
  }
}
@media (min-width: 1200px) {
  .user-list__login-icon {
    left: 15px;
  }
}

.stats {
  background-color: #292929;
  color: #ffffff;
}

@media (min-width: 768px) {
