.works-item__parameter-item {
  position: relative;
  width: auto;
  margin-bottom: 15px;
  margin-right: 30px;
  padding-left: 36px;
  font-size: 16px;
  text-transform: uppercase;
}

.works-item__parameter-item::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 45px;
  height: 21px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  transform: translate(-30%, -50%);
}

.works-item__parameter-item--haircut::before {
  background-image: url("../img/icons/scissors-icon.svg");
}

.works-item__parameter-item--shaving::before {
  background-image: url("../img/icons/beard-icon.svg");
}

.works-item__parameter-item--mustache::before {
  background-image: url("../img/icons/mustache-icon.svg");
}

.works-item__parameter-item:last-child {
  margin-right: 0;
}

@media (min-width: 768px) {
