    display: flex;
    align-items: stretch;
  }
  .works-item--work-of-month::before {
    top: 35px;
    bottom: auto;
    left: 0;
  }
  .works-item--work-of-month::after {
    top: 35px;
    bottom: auto;
    left: 160px;
